.input {
  padding: 20px;
  width: 320px;
  height: 60px;

  font-size: 16px;
  font-weight: 500;

  border-radius: 10px;
  border: 1px solid var(--border-color);

  color: var(--white);
  transition: all 0.3s ease;
}

.input:active, .input:focus {
  border: 1px solid var(--light-blue);
}